import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import store from './../store'

Vue.use(VueRouter)





  const routes = [
  {
    path: '/login',
    name: 'Login',
    
    component: () => import('../views/Login.vue')
  },
  {
    path: '/',
    component: Home,
    meta: {
      requierAuth: true
    },
    children: [
      {
        path: '/',
        name: 'Dashboard',
        component: () => import('../views/Dashboard.vue'),
        meta: {
          requierAuth: true
        },
      },
      {
        path: '/:collection',
        name: 'Items',
        component: () => import('../views/Items.vue'),
        meta: {
          requierAuth: true
        },
      },
      {
        path: '/:collection/new',
        name: 'Item',
        component: () => import('../views/Item.vue'),
        meta: {
          requierAuth: true
        },
      },
      {
        path: '/:collection/:id',
        name: 'ItemUpdate',
        component: () => import('../views/Item.vue'),
        meta: {
          requierAuth: true
        },
      }
    ]
  }
 
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})



router.beforeEach((to, from, next) => {
  const { requierAuth } = to.meta
  if(requierAuth && !store.getters['auth/isAuthenticated'])
    return next('/login')
  if(to.name == 'Login' && store.getters['auth/isAuthenticated'])
    return next('/')

  // if (store.getters['auth/isAuthenticated']) {

  //   console.log(to ,store.state.auth.user.rights)
  // }
  return next()
})
export default router
